import * as React from 'react';
import { RenderNodeProps, Controller, Block } from '@ali/4ever-cangjie';
import { Card } from '@ali/4ever-plugin-card';
import { PlaceholderWrapper } from '@ali/4ever-component';

export function Placeholder({ node }: { node: any }) {
  if (!Block.isBlock(node)) {
    return <></>;
  }
  const text = `${node.data.name || ''}暂无法解析`;
  return <PlaceholderWrapper title={text}>{text}</PlaceholderWrapper>;
}

const renderUnknownCard = {
  [Card.TYPE]: (props: RenderNodeProps, controller: Controller) => {
    return <Placeholder node={props.node} />;
  },
};

const createUnknownCardPlugin = () => ({
  renderNode: renderUnknownCard,
});

export default createUnknownCardPlugin;
